// دالة للحصول على صورة البروفيل بواسطة المعرف
export const getAvatarById = (id: string) => {
  return avatarCollection.find(avatar => avatar.id === id);
};

// دالة للحصول على صورة عشوائية
export const getRandomAvatar = () => {
  const randomIndex = Math.floor(Math.random() * avatarCollection.length);
  return avatarCollection[randomIndex];
};